

































import Vue from "vue";
import FormClient from "@/components/form-client/FormClient.vue";
export default Vue.extend({
  components: { FormClient },
  data() {
    return {
      dataPlans: [] as any,
      headers: [
        {
          text: "Plano",
          align: "start",
          sortable: false,
          value: "plano"
        },
        { text: "Preo", value: "precos.total" },
        { text: "Operadora", value: "operadora" },
        { text: "Abrangncia", value: "abrangencia" },
        { text: "Tipo Acomodao", value: "tipo_acomodacao" }
      ]
    };
  },
  methods: {
    returnDataPlans(resp: any) {
      this.dataPlans = resp.planos;
    }
  }
});
